/**
 * Site header
 */
.site-header {
  border-top   : 5px solid $grey-color-dark;
  border-bottom: 1px solid $grey-color-light;
  min-height   : $spacing-unit * 1.865;

  // Positioning context for the mobile navigation icon
  position: relative;

  background-color: $dark-color;

}

.site-title {
  @include relative-font-size(1.625);
  font-weight   : 300;
  line-height   : $base-line-height * $base-font-size * 2.25;
  letter-spacing: -1px;
  margin-bottom : 0;
  float         : left;

  color: $brand-color;

  &,
  &:visited {
    color: $brand-color;
  }
}

.site-nav {
  float      : right;
  line-height: $base-line-height * $base-font-size * 2.25;

  .nav-trigger {
    display: none;
  }

  .menu-icon {
    display: none;
  }

  .page-link {
    color      : $text-color;
    line-height: $base-line-height;

    // Gaps between nav items, but not on the last one
    &:not(:last-child) {
      margin-right: 20px;
    }
  }

  @include media-query($on-palm) {
    position        : absolute;
    top             : 9px;
    right           : $spacing-unit / 2;
    background-color: $background-color;
    border          : 1px solid $grey-color-light;
    border-radius   : 5px;
    text-align      : right;

    label[for="nav-trigger"] {
      display: block;
      float  : right;
      width  : 36px;
      height : 36px;
      z-index: 2;
      cursor : pointer;
    }

    .menu-icon {
      display    : block;
      float      : right;
      width      : 36px;
      height     : 26px;
      line-height: 0;
      padding-top: 10px;
      text-align : center;

      >svg {
        fill: $grey-color-dark;
      }
    }

    input~.trigger {
      clear  : both;
      display: none;
    }

    input:checked~.trigger {
      display       : block;
      padding-bottom: 5px;
    }

    .page-link {
      display: block;
      padding: 5px 10px;

      &:not(:last-child) {
        margin-right: 0;
      }

      margin-left: 20px;
    }
  }
}



/**
 * Site footer
 */
.site-footer {
  border-top: 1px solid $grey-color-light;
  padding   : $spacing-unit 0;
}

.footer-heading {
  @include relative-font-size(1.125);
  margin-bottom: $spacing-unit / 2;
}

.contact-list,
.social-media-list {
  list-style : none;
  margin-left: 0;
}

.footer-col-wrapper {
  @include relative-font-size(0.9375);
  color      : $grey-color;
  margin-left: -$spacing-unit / 2;
  @extend %clearfix;
}

.footer-col {
  float        : left;
  margin-bottom: $spacing-unit / 2;
  padding-left : $spacing-unit / 2;
}

.footer-col-1 {
  width: -webkit-calc(35% - (#{$spacing-unit} / 2));
  width: calc(35% - (#{$spacing-unit} / 2));
}

.footer-col-2 {
  width: -webkit-calc(20% - (#{$spacing-unit} / 2));
  width: calc(20% - (#{$spacing-unit} / 2));
}

.footer-col-3 {
  width: -webkit-calc(45% - (#{$spacing-unit} / 2));
  width: calc(45% - (#{$spacing-unit} / 2));
}

@include media-query($on-laptop) {

  .footer-col-1,
  .footer-col-2 {
    width: -webkit-calc(50% - (#{$spacing-unit} / 2));
    width: calc(50% - (#{$spacing-unit} / 2));
  }

  .footer-col-3 {
    width: -webkit-calc(100% - (#{$spacing-unit} / 2));
    width: calc(100% - (#{$spacing-unit} / 2));
  }
}

@include media-query($on-palm) {
  .footer-col {
    float: none;
    width: -webkit-calc(100% - (#{$spacing-unit} / 2));
    width: calc(100% - (#{$spacing-unit} / 2));
  }
}

/**
  * people
  */

.person {
  clip-path   : circle(50%);
  width       : 100px;
  float       : left;
  margin-right: 50px;
  clear       : left;
}

/**
  * Home
  */

@media screen and (min-width: 1010px) {
  .body-home {
    background-image     : url("/img/background.png");
    background-clip      : border-box;
    background-repeat    : no-repeat;
    background-attachment: fixed;
    color                : $light-color;
  }

  .wrapper {
      margin-left: 200px;
  }

}

@media screen and (max-width: 1009px) {
  .body-home {
    color                : $light-color;
    background-position-x: -200px;
    background-image     : url("/img/background.png");
    background-clip      : border-box;
    background-repeat    : no-repeat;
    background-attachment: fixed;
    // background-color: #163762;
  }

}

.home {
  margin-top: 85px;
}

.home blockquote p::before {
  content: "“";
}

.home blockquote p::after {
  content: "”";
}

.home blockquote {
  margin     : 0 0 1.5rem;
  line-height: 1.625rem;
  // padding: .5625rem 1.25rem 0 1.1875rem;
  border-left: 1px solid #ddd;
}

.home blockquote p {
  font-size     : 20px;
  line-height   : 28px;
  font-family   : $base-font-family;
  font-style    : italic;
  letter-spacing: 0px;
  color         : $light-color;
}



@media screen and (min-width: 1915px) {

  .logos {
    position: absolute;
    top     : 85px;
    z-index : -1;
    left    : 1696px;
    width   : 200px;
  }
}

@media screen and (min-width: 1160px) and (max-width: 1915px) {

  .logos {
    position: absolute;
    top     : 85px;
    z-index : -1;
    right   : 20px;
    width   : 200px;
  }

}

@media screen and (min-width: 400px) and (max-width: 1159px) {
  .logos {
    padding : 10px 200px;
    position: static;
    width   : 150px;
    margin-bottom: 100px;
  }
}

@media screen and (max-width: 400px) {
  .logos {
    padding : 10px 20px;
    position: static;
    width   : 150px;
    margin-bottom: 100px;
  }
}



/**
 * Page content
 */
.page-content {
  padding: $spacing-unit 0;
  flex   : 1;
}

.page-heading {
  @include relative-font-size(2);
}

.post-list-heading {
  @include relative-font-size(1.75);
}

.post-list {
  margin-left: 0;
  list-style : none;

  >li {
    margin-bottom: $spacing-unit;
  }
}

.post-meta {
  font-size: $small-font-size;
  color    : $grey-color;
}

.post-link {
  display: block;
  @include relative-font-size(1.5);
}



/**
 * Posts
 */
.post-header {
  margin-bottom: $spacing-unit;
}

.post-title {
  @include relative-font-size(1.625);
  line-height: 1;

  @include media-query($on-laptop) {
    @include relative-font-size(2.25);
  }
}

.post-content {
  margin-bottom: $spacing-unit;

  h2 {
    @include relative-font-size(2);

    @include media-query($on-laptop) {
      @include relative-font-size(1.75);
    }
  }

  h3 {
    @include relative-font-size(1.625);

    @include media-query($on-laptop) {
      @include relative-font-size(1.375);
    }
  }

  h4 {
    @include relative-font-size(1.25);

    @include media-query($on-laptop) {
      @include relative-font-size(1.125);
    }
  }
}
